<template>
  <list-page :title="$t('settings.title')">
    <cell
      :title="$t('settings.account.title')"
      arrow
      @click="toAccountSettings"
    />
    <cell
      :title="$t('settings.language')"
      :value="lang"
      arrow
      @click="toLanguage"
    />
    <cell
      :title="$t('settings.notification')"
      toggle
      :checked="notification"
      @change="switchNotification"
    />
    <cell
      :title="$t('settings.about.title')"
      arrow
      @click="toAbout"
    />
    <cell
      :title="$t('common.feedback.title')"
      arrow
      @click="toFeedback"
    />
    <cell
      :title="$t('settings.clear')"
      @click="toClearCache"
    />
  </list-page>
</template>

<script>
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'
import { langs } from '@/i18n/index.js'

export default {
  components: {
    ListPage,
    Cell
  },
  data () {
    return {
      notification: true
    }
  },
  computed: {
    lang () {
      let langKey = this.$i18n.locale
      let lang = langs.find(lang => lang.key === langKey)
      return lang ? lang.text : ''
    }
  },
  methods: {
    toAccountSettings () {
      this.$router.push({
        name: 'account-settings'
      })
    },
    toLanguage () {
      this.$router.push({
        name: 'language'
      })
    },
    switchNotification (checked) {
      console.log('switchNotification', checked)
      this.notification = checked
      this.$http.post('/api/app/user/notify/change', {
        receiveNotify: this.notification ? 1 : 0
      }, {
        login: true
      })
    },
    toAbout () {
      this.$router.push({
        name: 'about'
      })
    },
    toFeedback () {
      this.$router.push({
        name: 'feedback'
      })
    },
    toClearCache () {
      this.$features.confirm({
        title: this.$t('settings.clear'),
        message: this.$t('settings.clearTip')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
