<template>
  <list-page
    :title="$t('sign.forget.title')"
    :tip="$t('sign.forget.tip')"
    :button="$t('btn.next')"
    :disable="disable"
    @click="toNext"
  >
    <template #page>
      <transition name="slide-wrapper">
        <router-view :verify="verify" style="z-index: 9;"/>
      </transition>
    </template>
    <field
      v-model="email"
      :title="$t('sign.info.email')"
      :placeholder="$t('sign.info.email')"
      :error="error"
      @input="error = ''"
    />
  </list-page>
</template>

<script>
import { checkEmail } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      verify: null,
      email: '',
      error: ''
    }
  },
  computed: {
    disable () {
      return !this.email
    }
  },
  methods: {
    toNext () {
      let result = checkEmail(this.email)
      this.error = result.msg
      this.verify = null
      if (result.pass) {
        this.$http.post('/api/app/user/email/resetPwd/apply', {
          email: this.email
        }, {
          loading: true
        }).then(data => {
          this.verify = data
          this.$router.push({
            name: 'forget-verify'
          })
        }, err => {
          if (!err.system) {
            this.error = err.msg
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
