<template>
  <div class="l-page">
    <v-nav icon="cross" normal/>
    <div class="l-page__content c-tip">
      <img class="c-tip__img" src="~@/assets/result-send.png"/>
      <div class="c-tip__tip">{{ $t('sign.signup.done.title') }}</div>
      <div class="c-tip__note u-pre-wrap">{{ $t('sign.signup.done.tip', { email: email }) }}</div>
      <div class="c-tip__footer u-separate-reverse--farthest">
        <div class="c-tip__button is-alone" @click="toEmail">{{ $t('sign.signup.done.btn') }}</div>
      </div>
    </div>
    <div class="l-page__footer o-done is-sub u-pre-wrap">
      {{ $t('sign.signup.done.retry') }}
      <span
        :class="{ 'u-color': isSended, 'u-color--blue': !isSended }"
        @click="retry"
      >
        {{ $t('sign.signup.done.send') }}{{ countMsg }}
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      count: 0
    }
  },
  computed: {
    isSended () {
      return this.count > 0
    },
    countMsg () {
      return this.count > 0 ? `(${ this.count })` : ''
    },
    email () {
      let email = this.$route.query.email
      return email ? decodeURIComponent(email) : ''
    },
    token () {
      let token = this.$route.query.token
      return token ? decodeURIComponent(token) : ''
    }
  },
  methods: {
    retry () {
      if (this.email && this.token && this.count === 0) {
        this.count = 60
        this.$timer = setInterval(() => {
          if (this.count > 1) {
            this.count -= 1
          } else {
            this.count = 0
            clearInterval(this.$timer)
          }
        }, 1000)
      }
    },
    toEmail () {},
    check () {
      if (this.email && this.token) {
        this.$http.post('/api/app/user/email/active/status', {
          email: this.email,
          token: this.token
        }).then(data => {
          if (data.status === 2) {
            this.$features.back()
          } else {
            this.$timer = setTimeout(this.check, 5000)
          }
        }, () => {
          this.$timer = setTimeout(this.check, 5000)
        })
      }
    }
  },
  created () {
    this.$timer = -1
    this.check()
  },
  beforeDestroy () {
    clearInterval(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.o-done {
  margin-bottom: 65px;
  text-align: center;
}
</style>
