import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en_US from './lang/en_US.js'
import zh_CN from './lang/zh_CN.js'
import zh_HK from './lang/zh_HK.js'

Vue.use(VueI18n)

export const langs = [
  {
    key: 'en_US',
    text: 'English'
  },
  {
    key: 'zh_HK',
    text: '繁體中文（香港）'
  },
  {
    key: 'zh_CN',
    text: '简体中文'
  }
]

function getDefaultKey () {
  let defaultKey = window.localStorage.getItem('locale')
  if (!defaultKey) {
    // 需获取系统语言
    let language = 'en_US'
    switch (language) {
      case 'zh_HK':
      case 'zh_TW':
        defaultKey = 'zh_HK'
        break
      case 'en_US':
        defaultKey = 'en_US'
        break
      default:
        defaultKey = 'zh_CN'
        break
    }
  } else {
    let defaultLang = langs.find(lang => lang.key === defaultKey)
    if (!defaultLang) {
      defaultKey = 'zh_HK'
    }
  }
  window.localStorage.setItem('locale', defaultKey)
  return 'en_US' //defaultKey
}

const i18n = new VueI18n({
  locale: getDefaultKey(),
  messages: {
    en_US,
    zh_HK,
    zh_CN
  }
})

export default i18n

export function changeLang (langKey) {
  if (langKey !== i18n.locale) {
    let langLocale = langs.find(lang => lang.key === langKey)
    if (langLocale) {
      i18n.locale = langKey
      window.localStorage.setItem('locale', langKey)
      return true
    }
  }
  return false
}

export function getLanguageKey () {
  switch (i18n.locale) {
    case 'en_US':
      return 'en'
    case 'zh_HK':
      return 'tc'
    default:
      return 'sc'
  }
}
