<template>
  <div id="app">
    <transition :name="transition">
      <keep-alive>
        <router-view v-if="$route.meta.cache" />
      </keep-alive>
    </transition>
    <transition :name="transition">
      <router-view v-if="!$route.meta.cache" />
    </transition>
    <div v-if="isForceUpgrade" class="c-upgrade">
      <div class="c-upgrade__content">
        <van-loading class="u-color--white"/>
        <div class="is-content u-color--white u-separate">{{ $t('upgrade.upgrading') }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import * as SystemEvent from '@/code/system-event.js'
import { Loading } from 'vant'

export default {
  components: {
    [ Loading.name ]: Loading
  },
  data () {
    return {
      transition: 'slide-r2l',
      isForceUpgrade: false
    }
  },
  computed: {
    key () {
      return this.$route.fullPath
    }
  },
  methods: {
    checkDirection (to, from) {
      if (this.$back) {
        this.transition = 'slide-l2r'
      } else {
        if (to.meta.tabbar) {
          this.transition = 'slide-l2r'
        } else {
          this.transition = 'slide-r2l'
        }
      }
    },
    back (depth) {
      this.$back = true
      this.$router.go(depth)
    },
    shutdown (message) {
      this.$features.confirm({ message }).then(() => this.$native.shutdownApp())
    }
  },
  beforeMount () {
    let element = document.getElementById('loading')
    if (element) {
      document.body.removeChild(element)
    }

    this.$router.beforeEach((to, from, next) => {
      this.checkDirection(to, from)
      this.$nextTick(next)
      this.$back = false
    })

    this.$eventBus.$on('back', this.back)

    this.$eventBus.$on(SystemEvent.VERSION_UPGRADE, () => {
      this.isForceUpgrade = false
      this.shutdown(this.$t('upgrade.tip'))
    })

    this.$eventBus.$on(SystemEvent.VERSION_UPGRADE_FORCE, () => {
      this.isForceUpgrade = true
    })

    this.$eventBus.$on(SystemEvent.VERSION_UPGRADE_FAIL, code => {
      if (this.isForceUpgrade) {
        this.isForceUpgrade = false
        this.shutdown(this.$t(`upgrade.error.${ code }`))
      }
    })

    this.$native.updateH5({
      token: this.$store.state.authInfo.token
    })
  }
}
</script>

