<template>
  <list-page
    :title="$t('sign.verify.phone.title')"
    :tip="$t('sign.verify.phone.tip')"
    :button="$t('sign.verify.phone.btn')"
    :disable="disable"
    @click="toConfirm"
  >
    <template #nav>
      <span class="is-content--blue" @click="skip">{{ $t('btn.skip') }}</span>
    </template>
    <phone
      v-model="phone"
      :area-code="areaCode"
      :error="error"
      @change="onAreaCodeChange"
      @input="clean"
    />
  </list-page>
</template>

<script>
import { checkPhone } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Phone from '@/components/list/Phone.vue'

export default {
  components: {
    ListPage,
    Phone
  },
  data () {
    return {
      areaCode: '',
      phone: '',
      regExp: '',
      error: ''
    }
  },
  computed: {
    disable () {
      return !this.areaCode || !this.phone
    }
  },
  methods: {
    clean () {
      this.error = ''
    },
    onAreaCodeChange ({ areaCode, regExp }) {
      this.areaCode = areaCode
      this.regExp = regExp
      this.clean()
    },
    skip () {

    },
    toConfirm () {
      let result = checkPhone(this.areaCode, this.phone, this.regExp)
      if (result.pass) {
        this.toNext()
      } else {
        this.error = result.msg
      }
    },
    toNext () {
      this.$toast(this.$t('sign.verify.phone.title'))
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
