import { log } from '@/utils/log.js'
import EventBus from '@/utils/event-bus.js'
import * as SystemEvent from '@/code/system-event.js'

let hasNative = false

let api = null

function callHandler (funcName, data, useLog) {
  if (hasNative) {
    let dataInfo = data ? JSON.stringify(data) : 'null'
    log(`call native ${ funcName }: ${ dataInfo }`)
    return new Promise(resolve => {
      api.callHandler(funcName, data, function (res) {
        if (useLog !== false) {
          log(`native ${ funcName }: ${ res }`)
        }
        resolve(res)
      })
    })
  } else {
    return Promise.reject({ code: -1 })
  }
}

let readyCb = []

function emit (event, payload) {
  EventBus.$emit(event, payload)
}

const NativeEvent = {
  VERSION_UPGRADE: 300,
  VERSION_UPGRADE_FORCE: 301,
  VERSION_UPGRADE_DOWNLOAD_FAIL: 302,
  VERSION_UPGRADE_CHECK_FAIL: 303,
  VERSION_UPGRADE_SPACE_NOT_ENOUGH: 304,
  VERSION_UPGRADING: 305
}

const EventMap = {
  [NativeEvent.VERSION_UPGRADE]: SystemEvent.VERSION_UPGRADE,
  [NativeEvent.VERSION_UPGRADE_FORCE]: SystemEvent.VERSION_UPGRADE_FORCE,
  [NativeEvent.VERSION_UPGRADE_DOWNLOAD_FAIL]: SystemEvent.VERSION_UPGRADE_FAIL,
  [NativeEvent.VERSION_UPGRADE_CHECK_FAIL]: SystemEvent.VERSION_UPGRADE_FAIL,
  [NativeEvent.VERSION_UPGRADE_SPACE_NOT_ENOUGH]: SystemEvent.VERSION_UPGRADE_FAIL,
  [NativeEvent.VERSION_UPGRADING]: SystemEvent.VERSION_UPGRADING
}

function analysis (event, data = null) {
  let eventName = EventMap[event]
  if (eventName) {
    switch (eventName) {
      case SystemEvent.VERSION_UPGRADE_FAIL:
        data = event
        emit(eventName, event)
        break
      default:
        emit(eventName, data)
        break
    }
    emit(SystemEvent.NATIVE_EVENT, {
      type: eventName,
      data
    })
  }
}

function nativeEventCallback (data) {
  log(`nativeEventCallback: ${ data }`)
  data = JSON.parse(data)
  analysis(data.type, data.data)
}

window.setEventCallback = nativeEventCallback

function proxy (bridge) {
  if (!hasNative) {
    hasNative = true
    api = bridge

    bridge.registerHandler('setEventCallback', function (data) {
      nativeEventCallback(data)
    })

    readyCb.forEach(cb => cb())

    readyCb = null

    emit(SystemEvent.NATIVE_READY)
  }
}

const JsNativeApi = {
  ready () {
    if (hasNative) {
      return Promise.resolve()
    } else {
      return new Promise(resolve => readyCb.push(resolve))
    }
  },
  getNativeOS () {
    let os = window.localStorage.getItem('os')
    if (os) {
      return Promise.resolve(os)
    } else {
      return callHandler('getNativeOS', null).then(os => {
        window.localStorage.setItem('os', os)
        return os
      })
    }
  },
  scan () {
    return callHandler('scanQRCode', null).then(result => JSON.parse(result))
  },
  ringUp (phoneNumber) {
    if (phoneNumber) {
      return callHandler('ringUp', {
        phoneNumber
      }).then(result => {
        return result === 'true' ? true : false
      })
    } else {
      return Promise.reject({ code: -1 })
    }
  },
  pickimage () {
    return callHandler('pickimage', null, false).then(result => JSON.parse(result))
  },
  updateH5 (payload) {
    return callHandler('updateH5', payload).then(result => {
      return result === 'true' ? true : false
    })
  },
  shutdownApp () {
    return callHandler('shutdownApp', null).then(result => {
      return result === 'true' ? true : false
    })
  }
}

window.JsNativeApi = JsNativeApi

export default JsNativeApi

export function inject () {
  return new Promise(resolve => {
    window.setupWebViewJavascriptBridge = function (cb) {
      if (window.WebViewJavascriptBridge) {
        return cb(window.WebViewJavascriptBridge)
      } else {
        // 兼容android bridge
        document.addEventListener('WebViewJavascriptBridgeReady', function () {
          cb(window.WebViewJavascriptBridge)
        }, false)
      }
      if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(cb) }
      window.WVJBCallbacks = [cb]
      let WVJBIframe = document.createElement('iframe')
      WVJBIframe.style.display = 'none'
      WVJBIframe.src = 'https://__bridge_loaded__'
      document.documentElement.appendChild(WVJBIframe)
      setTimeout(function () { document.documentElement.removeChild(WVJBIframe) }, 0)
    }
    window.setupWebViewJavascriptBridge(function (bridge) {
      proxy(bridge)
      resolve()
    })
  })
}
