<template>
  <list-page
    :title="$t('sign.verify.code.title')"
    :tip="$t('sign.verify.code.sms')"
  >
    <div class="c-code" @click="showKeyboard = true">
      <div
        v-for="(msg, index) in codeMsg"
        :key="index"
        class="c-hlist-item--near c-code__item"
        :class="{ 'is-selected': index === codeIndex }"
      >{{ msg }}</div>
    </div>
    <van-number-keyboard
      :show="showKeyboard"
      @input="onInput"
      @delete="onDelete"
      @blur="showKeyboard = false"
    />
    <div class="is-sub u-pre-wrap u-align-center u-separate--farther">
      {{ $t('sign.verify.code.retry') }}
      <span
        :class="{ 'u-color': isSended, 'u-color--blue': !isSended }"
        @click="retry"
      >
        {{ $t('sign.verify.code.send') }}{{ countMsg }}
      </span>
    </div>
  </list-page>
</template>

<script>
import { NumberKeyboard } from 'vant'
import ListPage from '@/components/list/ListPage.vue'

export default {
  components: {
    [ NumberKeyboard.name ]: NumberKeyboard,
    ListPage
  },
  props: {
    areaCode: {
      type: String,
      default: ''
    },
    phone: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showKeyboard: false,
      code: '',
      count: 0
    }
  },
  computed: {
    isSended () {
      return this.count > 0
    },
    countMsg () {
      return this.count > 0 ? `(${ this.count })` : ''
    },
    codeIndex () {
      return this.showKeyboard ? this.code.length : -1
    },
    codeMsg () {
      let arr = []
      for (let i = 0; i < 4; i++) {
        arr[i] = i === this.codeIndex ? '' : this.code[i] || '-'
      }
      return arr
    }
  },
  methods: {
    retry () {
      if (this.areaCode && this.phone && this.count === 0) {
        this.count = 60
        this.$timer = setInterval(() => {
          if (this.count > 1) {
            this.count -= 1
          } else {
            this.count = 0
            clearInterval(this.$timer)
          }
        }, 1000)
      }
    },
    onInput (key) {
      if (this.showKeyboard && this.code.length < 4) {
        this.code += key
        if (this.code.length === 4) {
          this.showKeyboard = false
          this.toVerify()
        }
      }
    },
    onDelete () {
      if (this.showKeyboard) {
        this.code = this.code.slice(0, this.code.length - 1)
      }
    },
    toVerify () {

    }
  }
}
</script>

<style lang="scss" scoped>
.c-code {
  display: flex;
  justify-content: center;
  height: 138px;

  &__item {
    flex: 1;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 138px;
    font-size: 72px;
    color: $color--dark;
    border-radius: 8px;
    border: 2px solid $color-separate;

    &.is-selected {
      border: 4px solid $color-blue;
    }
  }
}
</style>
