import cfg from 'config'
import axios from 'axios'
import i18n, { getLanguageKey } from '@/i18n'
import store from '@/store'
import { isExpired } from './auth.js'
import { Toast } from 'vant'

axios.defaults.baseURL = cfg.baseURL
axios.defaults.timeout = cfg.timeout

function check (url, options) {
  let {
    auth = true, login = false
  } = options

  if (auth && isExpired()) {
    console.log(url, 'Auth required, but not')
    return false
  }

  if (login && !store.getters.isLogined) {
    console.log(url, 'Login required, but current state is not logined')
    return false
  }

  return true
}

function getHeader (type, options) {
  let {
    auth = true
  } = options

  let header = {
    'content-type': type || 'application/json'
  }

  if (auth) {
    header['optoken'] = store.state.authInfo.token
  }

  return header
}

function getCommonData (options) {
  let {
    login = false
  } = options

  let commonData = {
    pId: 1,
    lang: getLanguageKey()
  }

  if (login) {
    commonData['userIdType'] = store.state.authInfo.userIdType
    commonData['userId'] = store.state.authInfo.userId
  }

  return commonData
}

function addCommonData (formData, options) {
  let {
    login = false
  } = options

  formData.append('pId', 1)
  formData.append('lang', getLanguageKey())

  if (login) {
    formData.append('userIdType', store.state.authInfo.userIdType)
    formData.append('userId', store.state.authInfo.userId)
  }

  return formData
}

function createHttpReject (res, options = {}) {
  let {
    server = false
  } = options

  if (server) {
    Toast(i18n.t('errMsg.net', {
      code: res.statusCode
    }))
  }

  return {
    system: true,
    code: -9999,
    msg: res.data.error || res.data.message,
    statusCode: res.statusCode
  }
}

function createSystemReject (res, options = {}) {
  let {
    server = false, abort = false
  } = options

  if (server) {
    Toast(i18n.t('errMsg.sys'))
  }

  return {
    system: true,
    abort,
    code: -999,
    msg: res.message
  }
}

let source = axios.CancelToken.source()
export function clear () {
  if (source) {
    source.cancel('abort')
  }
  source = axios.CancelToken.source()
}

function createPost (url, data, options, flags) {
  return new Promise((resolve, reject) => {
    let {
      loading = false, server = false, globals = false,
      cancelToken = null
    } = flags

    options.cancelToken = cancelToken || !globals && source.token || null

    if (loading !== false) {
      Toast.loading()
    }

    axios.post(url, data, options).then(res => {
      if (loading !== false) {
        Toast.clear()
      }
      if (res.status === 200) {
        if (res.data.code === 0) {
          resolve(res.data.data)
        } else {
          console.log('request abnormal', url, res.data.code, res.data.msg)
          if (server && res.data.msg) {
            Toast(res.data.msg)
          }
          reject(res.data)
        }
      } else {
        console.log('request error', url, res.statusCode)
        reject(createHttpReject(res, options))
      }
    }, err => {
      console.log('request fail', url, err.message)
      let isAbort = /abort/.test(err.message)
      if (!isAbort && loading !== false) {
        Toast.clear()
      }
      reject(createSystemReject(err, isAbort ? { abort: true } : options))
    })
  })
}

// loading 显示loading图标
// server 显示服务器异常原因（必须有返回值）
// auth 需授权后才能调用
const http = {
  post (url, data = {}, options = {}) {
    if (!check(url, options)) {
      return Promise.reject({
        system: true,
        code: -10000
      })
    }

    return createPost(url, {
      ...getCommonData(options),
      ...data
    }, {
      headers: getHeader('application/json', options)
    }, options)
  },
  postForm (url, formData, options) {
    if (!check(url, options)) {
      return Promise.reject({
        system: true,
        code: -10000
      })
    }

    return createPost(url, addCommonData(formData, options), {
      headers: getHeader('application/json', options)
    }, options)
  },
  upload (url, data, options = {}) {
    if (!check(url, options)) {
      return Promise.reject({
        system: true,
        code: -10000
      })
    }

    let formData = new FormData()
    if (data.type === 'blob') {
      formData.append('picFile', data.file, `${ Date.now() }.jpeg`)
    } else {
      formData.append('picFile', data.file)
    }

    return createPost(url, addCommonData(formData, options), {
      headers: getHeader('application/json', options)
    }, options)
  },
  createSource () {
    return axios.CancelToken.source()
  }
}

export default http
