<template>
  <div class="l-page">
    <v-nav
      :title="name"
      :scroll="scrollTop"
      fixed
    />
    <template v-if="entity">
      <div class="l-page__content" @scroll="scroll">
        <banner
          :list="bannerResourceList"
          :auto="isBaseRoute"
        >
          <div v-if="needWait" class="o-tag is-hanging is-orange">
            <span class="t1">{{ entity.pflowInfo.entityWaitTime }}</span>
            <span class="s1">{{ $t('explore.detail.wait') }}</span>
          </div>
          <div v-if="isSuspend" class="o-tag is-hanging is-disable">
            <span>{{ $t('explore.detail.suspend') }}</span>
          </div>
        </banner>
        <information :entity="entity"/>
        <opening v-if="hasOperatingInfo" :entity="entity" :status="status"/>
        <opening-shows v-if="hasShows" :entity="entity"/>
        <extra :entity="entity" :status="status"/>
        <relation :entity="entity"/>
      </div>
      <v-footer :entity="entity"/>
    </template>
    <transition name="slide-wrapper">
      <router-view style="z-index: 9;"/>
    </transition>
  </div>
</template>

<script>
import TransitionWrapperMixin from '@/components/mixin/TransitionWrapper.js'
import Banner from '@/components/Banner.vue'
import Information from '@/components/map/Information.vue'
import Opening from '@/components/map/Opening.vue'
import OpeningShows from '@/components/map/OpeningShows.vue'
import Extra from '@/components/map/Extra.vue'
import Relation from '@/components/map/Relation.vue'
import VFooter from '@/components/map/Footer.vue'

export default {
  mixins: [ TransitionWrapperMixin ],
  components: {
    Banner,
    Information,
    Opening,
    OpeningShows,
    Extra,
    Relation,
    VFooter
  },
  data () {
    return {
      route: 'entity',
      id: '',
      entity: null,
      scrollTop: 0
    }
  },
  computed: {
    name () {
      return this.entity ? this.entity.name : ''
    },
    bannerResourceList () {
      return this.entity && this.entity.bannerResourceList || []
    },
    status () {
      return this.$features.getStatus(this.entity)
    },
    isSuspend () {
      return this.status.isSuspend
    },
    needWait () {
      return this.status.isOpeningNow && !this.isSuspend && this.entity.pflowStatus === 1 && this.entity.pflowInfo.entityWaitTime > 0
    },
    isShowsType () {
      return this.entity ? this.entity.typeId === 4 : false
    },
    hasOperatingInfo () {
      // 表演类无营业时间
      if (this.entity && !this.isShowsType) {
        return this.entity.pflowStatus === 1 || !!this.entity.operatorInfo
      }
      return false
    },
    hasShows () {
      // 表演类在suspend和close状态不显示表演时间
      if (this.entity && this.isShowsType) {
        let status = this.entity.pflowStatus === 1 ? this.entity.pflowInfo.entityStatus : this.entity.openStatus
        return status !== 'suspend' && status !== 'close'
      }
      return false
    }
  },
  methods: {
    showMsg (index) {
      let msg = this.entity.menu.msg[index]
      if (msg) {
        if (msg.content) {
          this.$features.alert({
            title: msg.title,
            message: msg.content,
            btn: msg.buttons && msg.buttons[0]
          }).then(() => this.showMsg(index + 1))
        } else {
          this.showMsg(index + 1)
        }
      }
    },
    scroll (e) {
      this.scrollTop = e.currentTarget.scrollTop
    },
    clear () {
      this.entity = null
      this.scrollTop = 0
    },
    refresh () {
      this.id = Number(this.$route.params.id)
      this.$http.post('/api/common/entity/detail', {
        entityId: this.id
      }, {
        server: true
      }).then(data => {
        this.entity = data
        if (this.entity && this.entity.menu && this.entity.menu.msg) {
          this.showMsg(0)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
