<template>
  <div class="l-page">
    <v-nav :title="$t('ai.title')"/>
    <div class="l-page__content l-flex--column">
      <router-link
        tag="div"
        class="o-photograph"
        :to="{ name: 'photograph' }"
      >
        <van-icon name="photograph"/>
        <div class="o-help" :class="{ 'is-hide': hide }">{{ $t('ai.help') }}</div>
      </router-link>
      <van-tabs
        class="l-flex__fill c-nav-tabs c-nav-tabs--options"
        type='none'
        v-model="index"
        :ellipsis="false"
        swipeable
        animated
        @change="getList"
      >
        <van-tab
          v-for="(type, oindex) in types"
          :key="type.id"
        >
          <template #title>
            <div
              class="o-option"
              :class="{ 'is-selected': oindex === index }"
            >
              {{ type.name }}
            </div>
          </template>
          <div
            v-if="type.status === 1"
            class="is-cover u-align-center"
          >
            <van-loading type="spinner"/>
          </div>
          <div
            v-if="type.status === -1"
            class="is-tip--dark"
          >
            {{ $t('common.empty') }}
          </div>
          <template v-if="type.status === 2">
            <animal
              v-for="(item, index) in list"
              :key="index"
              class="o-animal"
              :entity="item"
              large
            />
          </template>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { Tabs, Tab, Icon, Loading } from 'vant'
import Animal from '@/components/map/Animal.vue'

export default {
  components: {
    [ Tabs.name ]: Tabs,
    [ Tab.name ]: Tab,
    [ Icon.name ]: Icon,
    [ Loading.name ]: Loading,
    Animal
  },
  data () {
    return {
      hide: false,
      index: -1,
      types: []
    }
  },
  computed: {
    type () {
      return this.types[this.index]
    },
    list () {
      return this.type ? this.type.list : []
    }
  },
  methods: {
    changeType (index) {
      if (this.index !== index) {
        this.index = index
        this.getList()
      }
    },
    getList () {
      let type = this.type
      if (type && type.status === 0) {
        type.status = 1
        this.$http.post('/api/common/entity/list/animal', {
          typeId: type.id
        }).then(data => {
          if (type.timestamp !== data.updateTime) {
            type.timestamp = data.updateTime
            type.list = data.data
            type.status = type.list.length > 0 ? 2 : -1
          }
        }, () => type.status = 0)
      }
    }
  },
  created () {
    this.$http.post('/api/common/dic/values', {
      typeId: 'animalType'
    }).then(data => {
      this.types = data.map(type => {
        return {
          timestamp: 0,
          status: 0,
          list: [],
          ...type
        }
      })
      this.changeType(0)
    })

    let hasShow = window.localStorage.getItem('ai')
    if (hasShow && hasShow === 'triggered') {
      this.hide = true
      this.$timer = -1
    } else {
      window.localStorage.setItem('ai', 'triggered')
      this.$timer = setTimeout(() => this.hide = true, 3000)
    }
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.o-photograph {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 106px;
  height: 106px;
  color: $color-blue;
  background-color: $color-white;
  z-index: 99;
}

.o-help {
  position: absolute;
  left: 50%;
  bottom: 10px;
  padding: 24px 32px;
  font-size: 28px;
  color: $color-white;
  line-height: 34px;
  white-space: nowrap;
  transform: translate(-32px, 100%);
  border-radius: 16px;
  background-color: rgba($color-black, .7);
  pointer-events: none;

  &.is-hide {
    display: none;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 32px;
    transform: translate(-50%, -100%);
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-bottom: 14px solid rgba($color-black, .7);
  }
}

.c-nav-tabs--options {
  /deep/ .van-tabs__wrap {
    display: flex;
    align-items: center;
    height: 106px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0 , .1);
  }

  /deep/ .van-tab {
    padding: 0;
    margin-left: 16px;

    &:first-child {
      margin-left: 126px;
    }

    &:last-child {
      padding-right: 40px;
    }
  }

  /deep/ .van-tab__pane {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    padding: 40px;

    &::after {
      content: '';
      display: inline-block;
      flex: 1 0 0;
      min-width: 40%;
      max-width: 50%;
    }

  }
}

.o-animal {
  flex: 1 0 0;
  min-width: 40%;
  max-width: 50%;

  &:nth-child(n + 3) {
    margin-top: 40px;
  }

  &:nth-child(odd) {
    margin-right: 32px;
  }
}
</style>
