<template>
  <list-page :title="$t('settings.account.title')">
    <cell-panel
      :placeholder="$t('settings.account.email.title')"
      :title="$t('settings.account.iemail')"
      :value="email"
      :disable="loginByEmail"
      @click="toAddEmail"
    />
    <cell-panel
      :placeholder="$t('settings.account.phone.title')"
      :title="$t('settings.account.iphone')"
      :value="phone"
      @click="toAddPhone"
    />
    <cell
      v-if="loginByEmail"
      :title="$t('settings.account.pwd.title')"
      arrow
      @click="toChangePassword"
    />
    <cell
      :title="$t('settings.account.signOut')"
      @click="signOut"
    />
  </list-page>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { logout } from '@/utils/auth.js'
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'
import CellPanel from '@/components/list/CellPanel.vue'

export default {
  components: {
    ListPage,
    Cell,
    CellPanel
  },
  computed: {
    ...mapState(['userInfo']),
    ...mapGetters(['isLogined', 'loginByEmail']),
    email () {
      return this.userInfo && this.userInfo.email || ''
    },
    phone () {
      if (this.userInfo) {
        let { areaCode = '', phone = '' } = this.userInfo
        if (areaCode && phone) {
          return `+${ areaCode }${ phone }`
        }
      }
      return ''
    }
  },
  methods: {
    toAddEmail () {
      if (this.isLogined) {
        this.$router.push({
          name: 'add-email'
        })
      }
    },
    toAddPhone () {
      if (this.isLogined) {
        this.$router.push({
          name: 'add-phone'
        })
      }
    },
    toChangePassword () {
      if (this.isLogined) {
        this.$router.push({
          name: 'change-password'
        })
      }
    },
    signOut () {
      this.$features.confirm({
        title: this.$t('settings.account.signOut'),
        message: this.$t('settings.account.signOutTip')
      }).then(() => {
        this.$http.post('/api/common/user/logout', null, {
          globals: true,
          login: true
        })
        logout(() => this.$router.replace({
          name: 'home'
        }))
      })
    }
  }
}
</script>

