import store from '@/store/index.js'
import http, { clear } from './http.js'
import { clearMails } from '@/utils/common.js'
import { Toast } from 'vant'

function info () {
  return store.state.authInfo
}

export function save (data) {
  clearTimeout(timer)
  let authInfo = info()
  if (authInfo && data && data.type < authInfo.type) {
    return false
  }
  store.commit('authInfo', data)
  if (!data || data.type === 0) {
    store.commit('userInfo', null)
  }
  getUserInfo()
  return true
}

let timer = -1
function retry () {
  let authInfo = info()
  let time = authInfo.tokenExpire - Date.now()
  if (time < 3600000) {
    time = Math.floor(time / 3 * 2)
  } else {
    time -= 3600000
  }
  timer = setTimeout(authNomral, time)
}

export let fetching = false

export function authNomral (cb) {
  if (fetching) {
    return
  }

  let authInfo = info()
  if (authInfo && authInfo.type > 0) {
    return
  }

  fetching = true
  http.post('/api/common/user/token', {
    deviceId: 'app-device'
  }, {
    globals: true,
    auth: false
  }).then(data => {
    fetching = false
    save({
      type: 0,
      token: data.token,
      tokenExpire: data.tokenExpire
    }) && retry()
    cb && cb()
  }, () => {
    fetching = false
    this.start()
  })
}

export function isExpired () {
  let authInfo = info()
  if (!authInfo) {
    console.log('Unauthorized')
    logout()
    return true
  }

  let expire = authInfo.tokenExpire
  if (expire <= Date.now()) {
    console.log('Token is expired')
    logout()
    return true
  }

  // 提前10分钟获取
  if (expire <= Date.now() + 600000) {
    console.log('Token will expire in 10 minutes')
    authNomral()
  }

  return false
}

let source = null
export function getUserInfo () {
  let authInfo = info()
  if (source && (!authInfo || authInfo.type === 0)) {
    source.cancel('abort')
  } else if (authInfo && authInfo.type > 0 && !source) {
    source = http.createSource()
    http.post('/api/common/user/info', null, {
      globals: true,
      login: true,
      cancelToken: source.token
    }).then(data => {
      store.commit('userInfo', data)
    }, err => {
      if (!err.abort) {
        setTimeout(getUserInfo, 2000)
      }
    }).finally(() => source = null)
  }
}

export function logout (cb) {
  Toast.loading()

  clear()
  clearMails()

  save(null)
  authNomral(() => {
    try {
      cb && cb()
    } catch (e) {
      console.log(e)
    }
    Toast.clear()
  })
}

export function auth () {
  return new Promise(resolve => {
    let authInfo = window.localStorage.getItem('authInfo') || null
    if (authInfo) {
      authInfo = JSON.parse(authInfo)
      if (!authInfo.tokenExpire || authInfo.tokenExpire <= Date.now()) {
        console.log('Clear authorization')
        authInfo = null
        window.localStorage.removeItem('authInfo')
      }
    }
    if (authInfo) {
      save(authInfo)
      resolve()
    } else {
      authNomral(resolve)
    }
  })
}
