import store from '@/store/index.js'
import http from './http.js'
import { sortBySortIndex } from './sort.js'

function necessary (url, data, options, cb, fail) {
  http.post(url, data, options).then(cb, () => {
    if (options.login && store.getters.isLogined) {
      setTimeout(() => necessary(url, data, options, cb, fail), 2000)
    } else {
      fail && fail()
    }
  })
}

export function config () {
  necessary('/api/common/config', {}, { globals: true }, cfg => {
    store.commit('config', {
      manageApiServerUrl: cfg.manageApiServerUrl,
      picLoopTime: cfg.picLoopTime,
      sharePic: cfg.defaultPics[2] && cfg.defaultPics[2].url || '',
      maxEschedulerCount: cfg.maxEschedulerCount || 3,
      orderTimeoutLong: cfg.orderTimeoutLong || 15,
      orderTimeList: cfg.orderTimeList || [],
      afterOrderTimeList: cfg.afterOrderTimeList || []
    })
  })
}

export function country () {
  necessary('/api/common/country/list', {}, { globals: true }, data => {
    store.commit('countryList', data.countryList)
  })
}

function transformMail (mail, type) {
  return {
    type,
    id: mail.msgId,
    isFresh: mail.isRead === 0,
    origin: mail
  }
}

const mailCfg = {
  user: {
    type: 1,
    loading: false,
    timestamp: -1,
    key: 'userMails'
  },
  park: {
    type: 2,
    loading: false,
    timestamp: -1,
    key: 'parkMails'
  }
}

function mail (options) {
  if (options.loading) {
    return
  }

  options.loading = true
  necessary('/api/common/user/msg/list', {
    msgType: options.type
  }, {
    globals: true,
    login: true
  }, data => {
    options.loading = false
    if (data.updateTime !== options.timestamp) {
      options.timestamp = data.updateTime
      store.commit(options.key, data.data.sort(sortBySortIndex).map(mail => transformMail(mail, options.type)))
    }
  }, () => options.loading = false)
}

export function initMails (all = true) {
  if (all) {
    mail(mailCfg.user)
    mail(mailCfg.park)
  } else {
    mail(mailCfg.park, true)
  }
}

export function constant () {
  config()
  country()
}

export function reset () {
  config()

  store.commit('countryList', [])
  country()

  clearMails(true)
}

export function clearMails (pull = false) {
  Object.keys(mailCfg).forEach(key => {
    let options = mailCfg[key]
    if (options.timestamp !== -1) {
      options.timestamp = -1
      store.commit(options.key, [])
      pull && mail(mailCfg.options)
    }
  })
}
