<template>
  <list-page
    :title="$t('sign.contact.title')"
    :tip="$t('sign.contact.tip')"
    :button="$t('btn.next')"
    :disable="disable"
    @click="toNext"
  >
    <template #page>
      <transition :name="transition">
        <router-view
          style="z-index: 9;"
          :area-code="areaCode"
          :phone="phone"
        />
      </transition>
    </template>
    <field
      v-model="name"
      :title="$t('sign.contact.name')"
      :placeholder="$t('sign.contact.name')"
      maxlength="6"
      :error="nerror"
      @input="nerror = ''"
    />
    <div class="o-sms-tip u-separate--nearer">{{ $t('sign.contact.sms') }}</div>
    <phone
      class="u-separate"
      v-model="phone"
      :area-code="areaCode"
      :error="error"
      @change="onAreaCodeChange"
      @input="clean"
    />
  </list-page>
</template>

<script>
import { checkChineseName, checkPhone } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'
import Phone from '@/components/list/Phone.vue'

export default {
  components: {
    ListPage,
    Field,
    Phone
  },
  data () {
    return {
      name: '',
      nerror: '',
      areaCode: '',
      phone: '',
      regExp: '',
      error: '',
      transition: 'slide-r2l'
    }
  },
  computed: {
    disable () {
      return !this.areaCode || !this.phone
    }
  },
  methods: {
    clean () {
      this.error = ''
    },
    onAreaCodeChange ({ areaCode, regExp }) {
      this.areaCode = areaCode
      this.regExp = regExp
      this.clean()
    },
    toNext () {
      let nResult = checkChineseName(this.name)
      this.nerror = nResult.msg

      let pResult = checkPhone(this.areaCode, this.phone, this.regExp)
      this.error = pResult.msg

      if (nResult.pass && pResult.pass) {
        this.$router.push({
          name: 'contact-verify'
        })
      }
    }
  },
  beforeRouteUpdate (to, from, next) {
    if (to.name === 'contact') {
      this.transition = 'slide-l2r'
    } else {
      this.transition = 'slide-r2l'
    }
    next()
  }
}
</script>

<style lang="scss" scoped>
.o-sms-tip {
  font-size: 24px;
  color: $color;
  line-height: 36px;
  @include u-font;
}
</style>
